<template>
  <div class="container">
    <el-card>
      <el-form class="search" @submit.native.prevent>
        <el-row>
          <el-col :span="6">
            <el-form-item size="small" label="关键字" label-width="80px">
              <el-input v-model="keyword" placeholder="根据编号搜索"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" :offset="12">
            <el-form-item size="small" style="text-align: right">
              <el-button @click="keyword = ''">清除</el-button>
              <el-button @click="getDataList" type="primary">搜索</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <!-- 数据条目统计 -->
      <el-alert
        :title="`数据一共 ${count} 条`"
        type="info"
        :closable="false"
        :show-icon="true"
      >
      </el-alert>

      <!-- 数据列表 -->
      <el-table :data="list">
        <el-table-column width="220" prop="id" label="编号" />
        <el-table-column width="80" label="题型">
          <template #default="{row}">
            <template v-if="row.questionType == 1">单选</template>
            <template v-if="row.questionType == 2">多选</template>
            <template v-if="row.questionType == 3">简答</template>
          </template>
        </el-table-column>
        <el-table-column width="220" label="题目编号">
          <template #default="{row}">
            <el-link
              v-for="(el, i) in row.questionIDs"
              :key="i"
              type="primary"
              :underline="false"
              @click="handleShowPreview(el.id)"
              >{{ el.number }}</el-link
            >
          </template>
        </el-table-column>
        <el-table-column width="180" prop="addTime" label="录入时间" />
        <el-table-column min-width="80" prop="totalSeconds" label="答题时间(s)" />
        <el-table-column min-width="80" prop="accuracyRate" label="正确率(%)" />
        <el-table-column min-width="80" prop="userName" label="录入人" />
        <el-table-column width="80" label="操作">
          <template #default="{row}">
            <el-button
              @click="handleDel(row.id)"
              plain
              size="small"
              type="danger"
              circle
            >
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页功能 -->
      <el-pagination
        background
        layout="prev, pager, next, sizes, jumper"
        :page-sizes="[20, 30, 50, 100]"
        :total="count"
        :current-page="page"
        @size-change="handleSizeChange"
        @current-change="handleCurrentPageChange"
      ></el-pagination>
    </el-card>

    <!-- 题目预览弹出层 -->
    <el-dialog title="题目预览" :visible.sync="isPreviewShow">
      <questions-preview v-if="isPreviewShow" :questionId="questionId" />
    </el-dialog>
  </div>
</template>

<script>
import { randoms, removeRandoms } from '@/api/hmmm/questions.js'
import QuestionsPreview from '@/module-hmmm/components/questions-preview.vue'
export default {
  components: { QuestionsPreview },
  data() {
    return {
      list: [],
      page: 1,
      pagesize: 20,
      keyword: '',
      count: 0,
      isPreviewShow: false,
      questionId: ''
    }
  },
  async created() {
    this.getDataList()
  },
  methods: {
    handleSizeChange(val) {
      this.pagesize = val
      this.page = 1
      this.getDataList()
    },
    handleCurrentPageChange(val) {
      this.page = val
      this.getDataList()
    },
    async getDataList() {
      try {
        const { data } = await randoms({
          page: this.page,
          pagesize: this.pagesize,
          keyword: this.keyword
        })
        this.count = data.counts
        this.list = data.items
      } catch (err) {
        console.log(err)
      }
    },
    async handleDel(id) {
      this.$confirm('此操作将永久删除该题组，是否继续？', '提示', {
        type: 'warning'
      })
        .then(id => this.delRandoms(id))
        .catch(() => {})
    },
    async delRandoms(id) {
      try {
        await removeRandoms({ id })
        this.$message({
          message: '删除成功',
          type: 'success'
        })
        this.getDataList()
      } catch (err) {
        console.log(err)
      }
    },
    async handleShowPreview(id) {
      this.isPreviewShow = true
      this.questionId = id
    }
  }
}
</script>

<style scoped lang="less">
.container {
  padding: 0 10px;
  margin: 10px 0;
  .el-alert {
    margin-bottom: 15px;
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
    ::v-deep .el-pagination__jump {
      margin-left: 0;
    }
  }
}
</style>
